<template>
    <div class="weather-report">
        <header>
            <p class="city">
                {{weatherReport ? weatherReport.city : '--'}}
            </p>
            <p class="weather">
                {{live ? live[dayType + 'weather'] : '--'}}
            </p>
            <p class="temp">
                &nbsp;{{live ? live[dayType + 'temp'] + '°' : '--°'}}
            </p>
            <p class="elseInfo">
                <span class="weathertime">
                    {{live ? live.date : '--'}}&nbsp; {{live ? weekArr[live.week] : '--'}}&nbsp; 今天
                </span>
                <span class="weathertemp">
                    <span :style="{color: dayType != 'day' ? 'rgba(255,255,255,0.6)' : ''}">
                        {{live ? live.daytemp : '--'}}
                    </span>
                    &nbsp;&nbsp;
                    <span :style="{color: dayType != 'night' ? 'rgba(255,255,255,0.6)' : ''}">
                        {{live ? live.nighttemp : '--'}}
                    </span>
                </span>
            </p>
        </header>
    </div>

</template>

<script>
import weatherService from "@/api/weather-report";
export default {
  name: "weather-report",
  data() {
    return {
      weatherInfo: "",
      dayType: "",
      weatherReport: "",
      live: "",
      forecasts: "",
      weatherType: "",
      weekArr: [
        "",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
        "星期日"
      ]
    };
  },
  created() {
    // 简单判断白天及黑夜，18：00 - 6：00算黑夜
    if (new Date().getHours() >= 6 && new Date().getHours() < 18) {
      this.dayType = "day";
    } else {
      this.dayType = "night";
    }
  },
  mounted() {
    weatherService.getWeatherReport().then(res => {
      if (res.status === "1") {
        this.weatherReport = res.forecasts[0];
        this.live = this.weatherReport.casts[0];
        this.forecasts = this.weatherReport.casts.slice(1);
        if (this.live[`${this.dayType}weather`].indexOf("晴") >= 0) {
          this.weatherType = "fine";
        } else {
          this.weatherType = "cloudy";
        }
      }
    });
  }
};
</script>

