.weather-report {
  height: calc(100% - 34px);
  color: #ffffff;
  background-color: #0281d1 !important;
  //   background: url("../assets/images/tools/tqyb_bg.png") center no-repeat;
  background-size: cover;

  header {
    text-align: center;
    padding-top: 20px;
    border-bottom: 1px color(#fff, light) solid;

    p {
      margin: 0;
      padding: 0;
    }

    p.city {
      font-size: 12px;
      transform: scale(0.5);
    }

    p.weather {
      margin-top: 5px;
      font-size: 10px;
    }

    p.temp {
      font-size: 14px;
      margin-top: 10px;
    }

    p.elseInfo {
      margin: 30px 5px 5px 5px;
      text-align: left;
      font-size: 12px;

      .weathertemp {
        float: right;
      }
    }
  }
}
</style>


